    import axios from 'axios'
    import _findIndex from 'lodash/findIndex'
    import api from '../api/api'

    export default {
        name : "apps_frame",
        data () {
            return {
                url :decodeURIComponent(this.$route.query.url)+'?str=' + parseInt ( Math.random () * 10000000 )
            }
        },
        async mounted () {
            //记录终端id ，终端屏幕大小，谁，经纬度，
            let deviceInfo=window.nativejs.getDeviceInfo()
            if(deviceInfo){
              deviceInfo=JSON.parse(deviceInfo);
            }
            let data = {
              type : '1',
              appname : this.$route.query.name,
              user : this.$store.state.userObj.data?this.$store.state.userObj.data.id:""
            }
            Object.assign(data,deviceInfo);
            axios ( {
                url : api.report,
                method : 'post',
                data
            } )
            this.$store.commit ( 'changeHeaderName', this.$route.query.name )
            this.$store.commit ( 'changeHeaderBack', true )
            console.log ( '---------------------------' )
