unzip(ZipPath, item) {

let name = item.key;
return new Promise((resolve, reject) => {
var ZipExtractDirectory =
cordova.file.externalRootDirectory + config.appName + "/" + name;

var StatusCallback = function (status) {
if (status == 0) {
console.log("解压完成");
resolve(ZipExtractDirectory);
} else if (status == -1) {
console.log("解压失败");
}
};
var ProgressCallback = function (progressEvent) {
var percent = Math.round(
(progressEvent.loaded / progressEvent.total) * 100
);
item.progress = parseInt(percent / 2 + 50);



};
window.zip.unzip(
ZipPath,
ZipExtractDirectory,
StatusCallback,
ProgressCallback
);
});
},
writeFile(dirEntry, dataObj, filename = "version.txt") {
return new Promise((resolve, reject) => {
dirEntry.getFile(
filename,
{ create: true, exclusive: false },
function (fileEntry) {
fileEntry.createWriter((fileWriter) => {
fileWriter.onwriteend = () => {
console.log("Successful file write...");
resolve("success");
};
fileWriter.onerror = (e) => {
console.log(`Failed file write: ${e.toString()}`);
reject(e);
};
if (!dataObj) {
dataObj = new Blob(["some file data"], { type: "text/plain" });
}

fileWriter.write(dataObj);
});
},
(error) => {
reject(error);
}
);
});
},

