unInstall(item) {
let _this = this;
console.log(item.path);
window.resolveLocalFileSystemURL(item.path, function (fileEntry) {
fileEntry.removeRecursively(
function () {
if (item.fileType == "apk") {
window.nativejs.cleanWorkOrder();
}
/*todo
*  退订topic
* */

if (window.client)
window.client.unsubscribe(item.key, function (err) {});
if (window.localStorage.getItem("msgs")) {
let list = JSON.parse(window.localStorage.getItem("msgs"));
if (list[item.key]) {
delete list[item.key];
window.localStorage.setItem("msgs", JSON.stringify(list));
}
}
_this.$set(
_find(_this.dataList, { id: item.id }),
"installed",
false
);
_this.$set(
_find(_this.dataList, { id: item.id }),
"path",
undefined
);
_this.$set(
_find(_this.dataList, { id: item.id }),
"fileType",
undefined
);
},
function (err) {
console.error(JSON.stringify(err));
},
function () {
console.log("file not exist");
}
);
});
