open(item) {
let _this = this;
if (item.fileType == "apk") {
window.nativejs.openWorkOrder(cordova.file.externalRootDirectory);
setInterval(() => {
window.resolveLocalFileSystemURL(
cordova.file.externalRootDirectory,
function (fileEntry) {
let deviceInfo = JSON.parse(window.nativejs.getDeviceInfo());
let data = new Blob([deviceInfo.lng + "," + deviceInfo.lat], {
type: "text/plain",
});
_this.writeFile(fileEntry, data, "gps.txt");
}
);
}, 1000 * 10);
} else {
this.$router.push({
path: "/apps_frame",
query: {
url: encodeURIComponent(item.path + "/index.html"),
name: item.name,
},
});
}
},
},
