import utils from "../utils/utils";
// zuoyu 更改
import axios from "axios";
import api from "../api/api";
// ------
export default {
  name: "message",
  data() {
    return {
      dataList: [],
    };
  },
  async mounted() {
    let _this = this;
    await this.getList();
    // 这部分
    await this.getList2();
    // -----
    window["onAppMessage"] = (topic, msg) => {
      _this.getList();
    };
  },
  methods: {
    async getList() {
      utils.changeAppList().then((list) => {
        let msgs = JSON.parse(window.localStorage.getItem("msgs"));
        let topicList = this.$store.state.messageNum;
        for (let i in list) {
          list[i].msg = msgs[list[i].key][0].msg;
          list[i].title = msgs[list[i].key][0].title;
          list[i].ctime = msgs[list[i].key][0].ctime;
          list[i].count = topicList[list[i].key];
        }
        this.dataList = list;
      });

      // })
    },

    // 佐宇更改
    async getList2() {
      let list = {};
      if (window.localStorage.getItem("msgs")) {
        list = JSON.parse(window.localStorage.getItem("msgs"));
      }
      this.dataList = list[this.$route.query.key];
      axios({
        url: api.msgList,
        method: "get",
      }).then((res) => {
        if (res && res.data.result) {
          // alert(JSON.stringify(res));

          this.dataList = res.data.result;
        }
      });
    },
    // -----------
    enter(item) {
      this.$store.commit("changeMessageNum", { topic: item.key, num: 0 });
      let msgs = JSON.parse(window.localStorage.getItem("msgs") || "{}");
      for (let i in msgs[item.key]) {
        msgs[item.key][i]["hasRead"] = true;
      }
      window.localStorage.setItem("msgs", JSON.stringify(msgs));

      this.$router.push({
        path: "/messageDetail",
        query: {
          id: item.id,
          key: item.key,
        },
      });
    },
  },
};



