

import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import store from './vuex/store'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import mqtt from 'mqtt'
import VueScroller from 'vue-scroller'
import axios from 'axios'
import Vconsole from "vconsole";
new Vconsole();
Vue.use(axios)
Vue.use(VueScroller)
Vue.use(Vuex)
window.mqtt = mqtt
Vue.config.productionTip = false
Vue.component('x-dialog', XDialog)
Vue.component('x-progress', XProgress)
Vue.component('toast', Toast)
Vue.component('badge', Badge)
Vue.component('x-button', XButton)
/* eslint-disable no-new */
router.beforeResolve((from, to, next) => {
console.log(to)
let showBack = 0
if (to.path === '/app') {
showBack = 0
} else {
showBack = 1
}
window.sessionStorage.setItem('showBack', showBack)
next()
})
const bus = new Vue({
store,
router
})
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})



