  import axios from 'axios'
  import api from '../api/api'

  export default {
    name : "messageDetail",
    data () {

      return {
        dataList : [],
        appDetail : {}
      }
    },
    async mounted () {
      await this.getOnlineText()
      // 佐宇更改
        this.$store.commit ( 'changeHeaderBack', true )
      let _this=this
       _this.getList()
       window['onAppMessage']=(topic,msg)=>{
         _this.getList()
       }
    },
    beforeDestroy(){
      this.$store.commit ( 'changeHeaderBack', false )
       this.$store.commit("changeHeaderName", '');
    },
    methods : {
      async getOnlineText(){
        axios({
          url:api.msgDetail,
          method:'post',
          data:{
            key:this.$route.query.key
          }
        }).then(res=>{
          let msgList = []
          for ( let item of res.data.result ) {
            let obj = JSON.parse ( item[ 'payload' ] )
            obj[ 'hasRead' ] = true
            msgList.unshift ( obj )
          }
          this.dataList = msgList
          this.$refs.my_scroller.finishPullToRefresh ();
          let list = {}
          if ( window.localStorage.getItem ( 'msgs' ) ) {
            list = JSON.parse ( window.localStorage.getItem ( 'msgs' ) )
          }
          list[ this.$route.query.key ] = msgList
          window.localStorage.setItem ( 'msgs', JSON.stringify ( list ) )
        })



      },
      async getList () {
        let list = {}
        if ( window.localStorage.getItem ( 'msgs' ) ) {
          list = JSON.parse ( window.localStorage.getItem ( 'msgs' ) )
        }
        this.dataList = list[ this.$route.query.key ]
        axios( {
          url : api.getDetail,
          method : 'post',
          data : {
            id : this.$route.query.id
          }
        } ).then( appDetail=>{
          if ( appDetail && appDetail.data.result ) {
            this.$store.commit ( 'changeHeaderName', appDetail.data.result.name )
            this.$store.commit ( 'changeHeaderBack', true )
            this.appDetail = appDetail.data.result
          }
        })

      }
    }
  }



