import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home.vue'
import app from '@/pages/app.vue'
import apps_frame from '@/pages/apps_frame.vue'
import setUp from '@/pages/setUp.vue'
import index from '@/pages/index.vue'
import message from '@/pages/message.vue'
import messageDetail from '@/pages/messageDetail.vue'
import my from '@/pages/my.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: home,
      redirect:'app',
      children:[
        {
          path:'index',
          name : '首页',
          component : index
        },
        {
          path:'app',
          name : '应用',
          component : app,
        },
        {
          path : 'setUp',
          name : '管理应用',
          component : setUp,
        },
        {
          path : 'message',
          name : '消息',
          component : message,
        },
        {
          path : 'my',
          name : '我的',
          component : my,
        },

        {
          path : 'messageDetail',
          name : '消息详情',
          component : messageDetail,
        },
        {
          path : '/apps_frame',
          name : '应用窗口', 
          component : apps_frame,
        },
      ]
    },

  ]
})



