import config from "./config";
let getList = (resolve, reject) => {

  console.log('111111!!!!!!!!!!!!!!!')
  if (cordova.file) {
    console.log(cordova.file.externalRootDirectory + config.appName + '/')
    let url = cordova.file.externalRootDirectory + '/'
    let arr = []


    window.resolveLocalFileSystemURL(url, function (rootEntery) {
      rootEntery.getDirectory(config.appName, { create: true }, function (directory_entry) {
        directory_entry.createReader().readEntries(  //如果 entry 已经是 DirectoryEntry 可以直接从这步开始
          function (entry_array) {
            if (entry_array.length) {
              for (let index in entry_array) {
                //读取
                // window.resolveLocalFileSystemURL ( url, function ( directory_entry ) {
                
                if (entry_array[index].name.indexOf(".zip") !== -1) {
                  continue
                } 
                try {
                  entry_array[index].getFile(
                    "version.txt",
                    { create: false, exclusive: false },
                    function (file_entry) { 
                      file_entry.file(  //如果 entry 已经是 FileEntry 可以直接从这步开始
                        function (file) {
                          var reader = new FileReader();
                          reader.onloadend = function () {

                            let dataRes = this.result
                            arr.push(JSON.parse(dataRes))
                            if (index == entry_array.length - 1) { 
                              console.log(JSON.stringify(arr))
                              console.log(resolve)
                              resolve(arr)
                            }
                          };
                          reader.onerror = function () {
                          };
                          reader.readAsText(file);
                        },
                        function (file_error) { 
                          reject(file_error)
                        }
                      );
                    },
                    function (file_error) {
                      reject(file_error)
                    }
                  );
                } catch (err) {
                  // alert(err)
                }
                // })
              }
            } else {
              resolve(arr)
            }

          },
          function (file_error) {
            reject(file_error)
          }
        );
      })

    }, function (err) {
      console.log(JSON.stringify(err))
    });
  }
  else {
    setTimeout(() => {
      getList(resolve, reject)
    }, 1000)

  }

}
export default {
  changeAppList() {
    return new Promise((resolve, reject) => {

      getList(resolve, reject)


    })


  }
}



