import config from "../utils/config";
import utils from "../utils/utils";
import axios from "axios";
import _findIndex from "lodash/findIndex";
import _find from "lodash/find";
import _clone from "lodash/cloneDeep";
import api from "../api/api";
export default {
  data() {
    return {
      text: "",
      dataList: [],
    };
  },
  async mounted() {
    let _this = this;
    console.log("========");

    console.log(api.getList);
    utils
      .changeAppList()
      .then((arr) => {
        console.log("app内dataList");
        console.log(JSON.stringify(arr));
        _this.dataList = _clone(arr);

        let userObj = window.nativejs.getUserInfo() || ""; 
        axios
          .get(api.getList, {
            params: {
              //临时注释,将通过公司名进行比对暂时传空
              // filter: JSON.parse(userObj).data.com
              filter: ""
            },
          })
          .then((res) => {
            if (res && res.data) {
              let tempList = [];
              for (let item of res.data.result) {
                let listItem = _find(arr, { id: item.id });
                if (listItem) {
                  if (listItem.version !== item.version) {
                    let tempArr = this.$store.state.updatedApps;
                    tempArr.push(item);
                    this.$store.commit("changeShowDot", tempArr);
                  }
                  tempList.push(listItem);
                }
              }

              _this.dataList = _clone(tempList);
              console.log("请求回来dataList");
              console.log(JSON.stringify(_this.dataList));
              console.log( "打印请求的列表： " + JSON.stringify(_this.dataList));
            }
          });
      })
      .catch(function (err) {
        console.log(err);
      });
  },

  methods: {
    // test(){
    //   alert (window.nativejs.test1());
    // },
    // test2(){
    //   let address = window.nativejs.test1();
    //   address=address.substring(9,address.length-1);
    //   alert(address)
    //   alert(window.nativejs.test2(0x00010000,address));
    // },
    open(item) {
      this.$router.push({
        path: "/apps_frame",
        query: {
          url: encodeURIComponent(item.path + "/index.html"),
          name: item.name,
        },
      });
      // let a='11111'
      // let _this=this
      // console.log ( '1111111111' );
      // let ref = window.open (  )
      // console.log(ref)
      // // ref.addEventListener('message', function (params) {
      // //   // params.data 就是消息
      // //   console.log(params.data)
      // // });
      // ref.addEventListener('loadstop', function () {
      //
      //   ref.executeScript({
      //     code: `
      //       function test(){
      //         return ${_this.openCamera}
      //       }
      //     `
      //   });
      // });
    },
    turnSet() {
      this.$router.push({
        path: "setUp",
      });
    },
  },
  watch: {},
};



