
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Popup from 'vux/src/components/popup/index.vue'

import utils from '../utils/utils'
import axios from 'axios'
import api from '../api/api'
import config from '../utils/config'
import _findIndex from 'lodash/findIndex'
export default {
components: {
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Popup
},
data() {
return {

badge: '',


selected: this.$route.path,
showMenus: false
}
},
watch: {













},
mounted() {
window.test = this
console.log('home111111111111111111111')






















let clientId = ''
if (window.localStorage.getItem('clientId')) {
clientId = window.localStorage.getItem('clientId')
} else {
clientId = 'user' + parseInt(Math.random() * 100000000)
window.localStorage.setItem('clientId', clientId)
}

this.initWebSocket(clientId)


this.computedMessageCount()

setTimeout(() => {
test.checkAppUpdate()
}, 2000)


},
methods: {
closeTab() {
this.$router.replace('/app')
},
async checkAppUpdate() {
let userObj = window.nativejs.getUserInfo() || ''
axios
.get(api.getList, {
params: {


filter: ''
}
})
.then((res) => {
utils.changeAppList().then((list) => {
if (res && res.data.result) {
for (let item of list) {
let index = _findIndex(res.data.result, { id: item.id })
if (item.version !== res.data.result[index].version) {
console.log('11111111111')
let tempArr = this.$store.state.updatedApps
tempArr.push(item)
this.$store.commit('changeShowDot', tempArr)
}
}
} else {
window.navigator.dialogsPlus.showNotice('服务器访问异常,请检查网络')
}
})
})
},
computedMessageCount(msgsArr) {
let arr = []
if (msgsArr) {
arr = msgsArr
} else {
if (window.localStorage.getItem('msgs')) {
arr = JSON.parse(window.localStorage.getItem('msgs'))
}
}

for (let i in arr) {
let count = 0
for (let j in arr[i]) {
if (!arr[i][j].hasRead) {
count++
}
}
this.$store.commit('changeMessageNum', { topic: i, num: count })
}
},
initWebSocket(clientId) {
console.log('aaaaaaa')








let _this = this



window.client = window.mqtt.connect(api.mqttUrl, {
clean: false,
clientId: clientId
})

let data = {}
let userObj = window.nativejs.getUserInfo() || ''
if (userObj) {
userObj = JSON.parse(userObj).data
userObj['uid'] = userObj['id']
delete userObj['id']
data['data'] = userObj
}
if (clientId) {
data['clientId'] = clientId
}
console.log('110000--------')
axios({
url: api.login,
method: 'post',
data
}).then((res) => {
console.log('------==========--------!!!!!!!=========')
console.log(JSON.stringify(res))
})












setInterval(() => {
let deviceInfo = JSON.parse(window.nativejs.getDeviceInfo())
let userInfo = window.nativejs.getUserInfo()
if (userInfo) {
window.resolveLocalFileSystemURL(cordova.file.externalRootDirectory, function(fileEntry) {
let data = new Blob([deviceInfo.lng || 0 + ',' + deviceInfo.lat || 0], { type: 'text/plain' })
let user = new Blob([userInfo], { type: 'text/plain' })
_this.writeFile(fileEntry, user, 'user.txt')
_this.writeFile(fileEntry, data, 'gps.txt')
})
}
}, 1000 * 60 * 10)









client.on('message', function(topic, message, c, d) {


console.log(JSON.stringify(c))
console.log(topic.toString() + '--' + message.toString())
let toClientId = JSON.parse(message.toString()).clientId
console.log(clientId + '---' + toClientId)
console.log(toClientId && toClientId != clientId)
if (toClientId && toClientId != clientId) {
return
}
let msgs = {}
if (window.localStorage.getItem('msgs')) {
msgs = JSON.parse(window.localStorage.getItem('msgs'))
}

if (!msgs[topic]) {
msgs[topic] = []
}
msgs[topic].unshift(JSON.parse(message.toString()))

window.localStorage.setItem('msgs', JSON.stringify(msgs))
_this.computedMessageCount(msgs)
let msgObj = JSON.parse(message.toString())

cordova.plugins.notification.local.schedule({
id: parseInt(Math.random() * 100000000),
title: msgObj.name,
text: msgObj.msg
})
if (window.onAppMessage) {
window.onAppMessage(topic, message)
}




})
},
writeFile(dirEntry, dataObj, filename) {
return new Promise((resolve, reject) => {
dirEntry.getFile(
filename,
{ create: true, exclusive: false },
function(fileEntry) {
fileEntry.createWriter((fileWriter) => {
fileWriter.onwriteend = () => {
console.log('Successful file write...')
resolve('success')
}
fileWriter.onerror = (e) => {
console.log(`Failed file write: ${e.toString()}`)
reject(e)
}
if (!dataObj) {
dataObj = new Blob(['some file data'], { type: 'text/plain' })
}

fileWriter.write(dataObj)
})
},
(error) => {
reject(error)
}
)
})
},
readFile(url) {
return new Promise((resolve, reject) => {})
}
}
}



