    import axios from 'axios'
    import api from './api/api'

    export default {
        name : 'App',
        mounted () {
            // window.open = cordova.InAppBrowser.open;
          console.log ( 'APP=================' )
          //   console.log ( decodeURIComponent ( this.$route.query.userObj ) )
          //   window.nativejs.test1()
            let userObj=window.nativejs.getUserInfo()
          console.log(userObj)
          console.log('=====================')
            if(userObj){
                userObj = JSON.parse ( userObj)
                this.$store.commit ( 'changeUserObj', userObj )

                axios ( {
                    url : api.report,
                    method : 'post',
                    data : {
                        type : '2',
                        user : userObj.data.id
                    }
                } )
            }



        },
        methods : {
          // test(){
          //   let sb=window.nativejs.test1()
          //   this.$refs['audio'].play()
          //   alert(sb);
          //
          // },
        }
    }



